body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Playfair Display', sans-serif;
}
.navbar{
	/* background: black; */
	background: 'transparent';
	height: 80px;
	display: flex;
	width: 100vw;
	justify-content: center;
	font-weight: 700;
	position: fixed;
	transition: -webkit-transform 1000ms;
	transition: transform 1000ms;
	transition: transform 1000ms, -webkit-transform 1000ms;
}

.navbar.move{
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
}

.navlink{
	color: #fff;
	font-size: 2rem;
	display: flex;
	align-items: center;
	text-decoration: none;
	cursor: pointer;
}


@media screen and (max-width: 530px) {
  .navlink {
    position: absolute;
    top: 10px;
    left: 25px;
  }

	.desktop{
		display:none;
	}
}

.navicon{
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	cursor: pointer;
	color: #fff;
	-webkit-transform: translate(100%, 50%);
	        transform: translate(100%, 50%);
	font-weight: bold;
	font-size: 2rem;
};

.container{
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2)), url(/restaurant_landing_page/static/media/burger.9d7fe2c8.jpg);
	height: 100vh;
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
}

.content{
	height: calc(100vh - 80px);
	max-height: 100%;
	/* padding: 0rem calc(100vw/2); */
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.contentItems{
	/* background: #f76d2844; */
	display: flex;
  width: clamp(100%,650,100%);
	flex-direction: column;
	height: 100vh;
	justify-content: center;
	margin-left: 50px;
	margin-top: 100px;
	align-items: flex-start;
	padding: 1rem 3rem;
	width: 700px;
	color:white;
}
@media screen and (max-width: 650px) {
  .contentItems{
  width: 100%;

  }
}
.navHead{
	font-size: clamp(1rem, 3rem, 5rem);
	margin-bottom: 2rem;
	letter-spacing: 3px;
	line-height: 1;
}

p{
	/* font-size: clamp(2rem, 2.5vw, 3rem); */
	font-size: clamp(1rem, 1.2rem, 1.4rem);
	/* font-size: clamp(0.5rem, 1.2rem, 2rem); */
	/* font-size: 1rem; */
	margin-bottom: 2rem;
	line-height: 1.3;
  font-family: 'Open Sans', sans-serif;
}

.btn{
	font-size: 1.8rem;
	padding: 1rem 2rem;
	border:solid 5px #F76D28;
	color: white;
	transition: 0.2s ease-out;
	margin-bottom: 10px;
	border-radius: 5px;
}

.btn:hover {
		background: #F76D28;
		transition: 0.2s ease-out;
		cursor: pointer;
		-webkit-transform: scale(1.05);
		        transform: scale(1.05)
}
.sideContainer{
	position: fixed;
	z-index: 100;
	width: 350px;
	height: 100%;
	background: #201414;
	display: grid;
	align-items: center;
	top: 0;
	transition: left 0.3s ease-in-out;
	left: -500px;
}

.sideContainer.show{
  left:0;
}
@media screen and (max-width: 400px) {
  .sideContainer{
    width: 100%;
  }
}

/* export const CloseIcon = styled(FaTimes){
	color: #000;
} */

.close{
	position: absolute;
	top: 1.2rem;
	right: 1.5rem;
	background: transparent;
	border: transparent;
	font-size: 1.4rem;
	cursor: pointer;
	outline: none;
	color: #fdfdfd;
  transition: -webkit-transform 0.5s ease-in;
  transition: transform 0.5s ease-in;
  transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
}
.close:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3)
}

.sideMenu{
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: repeat(4, 80px);
	text-align: center;
  margin-bottom: 5rem;
}

@media screen and (max-width: 480px) {
  .sideMenu {
    grid-template-rows: repeat(4, 60px);
  }
}

.sideLink{
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.5rem;
	text-decoration: none;
	list-style: none;
	transition: 0.2s ease-in-out;
	color: #fdfdfd;
	cursor: pointer;
}

.sideLink:hover {
  color: #F76D28;
  transition: 0.2s ease-in-out;
}

.btnWrap{
	display: flex;
	justify-content: center;
  margin-top: 3rem;
}

.sideRoute{
	background: #F76D28;
	white-space: nowrap;
	padding: 16px 64px;
	color: #fff;
	font-size: 16px;
	outline: none;
	border: none;
	cursor: pointer;
	transition: 0.2s ease-in-out;
	text-decoration: none;
}

.sideRoute:hover {
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}
